<script>
// @ is an alias to /src
import Cinema from "@/components/Cinema.vue";
import AllCity from "../components/AllCity.vue"
import Make from "../components/Make.vue"
import Service from "../components/Service.vue"
export default {
  name: "movie",
  components: {
    Cinema,
    AllCity,
    Make,
    Service
  },
  data() {
    return {
      time: "2019-05-12",
      index:3,
      indexcurr:0,
      indexcurrs:1,
      indexcurrss:2,
      cityShow:false,
      makeShow:false,
      characteristicShow:false,
      flag : true,
    };
    
  },
  methods: {
    show(e){
      if(this.flag){
        this.index = $(e.target).index();
        $(e.target).addClass("action").siblings().removeClass("action");
        this.flag = false;
      }else{
         this.index = 3;
       this.flag = true;
       $(e.target).removeClass("action");
      }
    }
  }
}
