<template>
    <div>
        <ul>
            <li v-for="(item, index) in subItems" :key="index" @click="toggle(index)"
   :class="{'action':index ==checkindex }">
                <span>{{item.name}}</span>
                <label>{{item.count}}</label>
            </li>
        </ul>
    </div>
</template>
<style lang="less" scoped>
div{
    position: absolute;
    top:50;
    background-color: white;
    width: 100%;
    ul{
        list-style: none;
        margin: 0;
        padding: 0;
        li{
            display: flex;
            text-align: center;
            margin: 15px;
            border-bottom: 1px solid  #e5e5e5;
            justify-content:space-between;
            line-height: 40px;
            font-size: 16px;
            color: #777;
        }
        .action{
        background : #f5f5f5;
        color: #e54847;
      }
    }
}
</style>

<script>
export default {
    name:'make',
    data() {
        return {
      subItems: null,
      checkindex:0
        }
    },
    created() {
    this.$axios
      .get(
        "http://api.kele8.cn/agent/http://m.maoyan.com/ajax/filterCinemas?ci=30"
      )
      .then(result => {
        this.subItems = result.data.brand.subItems;
        console.log(result.data);
      });
  },
  methods:{
      toggle (index) {
  this.checkindex = index
  }
  }
}
</script>
