<template>
  <div class="allcity">
    <div class="top">
      <p @click="districtclick" class="active">商圈</p>
      <p @click="subwaycilck">地铁</p>
    </div>

    <div id="dis">
      <div class="mide">
        <ul class="left">
          <li
            class="active"
            v-for="(item, index) in district"
            :key="index"
            @click="toggle(index)"
   :class="{'action':index ==checkindex }">{{item.name}}({{item.count}})</li>
        </ul>
        <ul class="right"></ul>
      </div>
    </div>
    <div id="box">
      <div class="subway">
        <ul class="left">
          <li
            class="active"
            v-for="(item, index) in subway"
            :key="index"
           @click="toggle(index)"
   :class="{'action':index ==checkindex }">{{item.name}}({{item.count}})</li>
        </ul>
        <ul class="right"></ul>
      </div>
    </div>
  </div>
</template>
<style lang="less" scoped>
.allcity {
  position: absolute;
  background-color: white;
  width: 100%;
  top:50;
  .top {
    display: flex;
    line-height: 30px;
    p {
      text-align: center;
      flex-grow: 1;
      color: black;
      font-size: 16px;
    }
    .active {
      color: #e54847;
      border-bottom: 1px solid #e54847;
    }
     
  }
  #box {
    display: none;
    .subway {
      display: flex;

      .left {
        flex-grow: 1;
        margin: 0;
        padding: 0;
        list-style: none;
        li {
          line-height: 40px;
          color: #777;
          font-size: 14px;
          text-align: center;
        }
      }
      .action{
        background : #f5f5f5;
        color: #e54847 !important;
      }
      .right {
        margin: 0;
        padding: 0;
        flex-grow: 5;
        list-style: none;
        background: #f5f5f5;
      }
    }
  }
  #dis {
    .mide {
      display: flex;

      .left {
        flex-grow: 1;
        margin: 0;
        padding: 0;
        list-style: none;
        li {
          line-height: 40px;
          color: #777;
          font-size: 14px;
          text-align: center;
        }
        .action{
        background : #f5f5f5;
        color: #e54847;
      }
      }
      .right {
        margin: 0;
        padding: 0;
        flex-grow: 5;
        list-style: none;
        background: #f5f5f5;
      }
    }
  }
}
</style>
<script>
export default {
  name: "allcity",
  data() {
    return {
      district: null,
      subway: null,
      checkindex:0
    };
  },
  methods: {
    subwaycilck(e) {
      document.getElementById("dis").style.display = "none";
      document.getElementById("box").style.display = "block";
      $(e.target)
        .addClass("active")
        .siblings()
        .removeClass("active");
    },
    districtclick(e) {
      document.getElementById("dis").style.display = "block";
      document.getElementById("box").style.display = "none";
      $(e.target)
        .addClass("active")
        .siblings()
        .removeClass("active");
    },
    toggle (index) {
  this.checkindex = index
  }
  },
  created() {
    this.$axios
      .get(
        "http://api.kele8.cn/agent/http://m.maoyan.com/ajax/filterCinemas?ci=30"
      )
      .then(result => {
        this.district = result.data.district.subItems;
        this.subway = result.data.subway.subItems;
        console.log(result.data);
      });
  },
  // updated() {
  //   let li = document.getElementsByClassName("active");
  //   for (let i = 0; i < li.length; i++) {
  //     let lis = li[i];

  //     lis.onmouseover = function() {
  //       lis.style.backgroundColor = "#f5f5f5";

  //       lis.style.color = "#e54847";
  //     };
  //     lis.onmouseout = function() {
  //       lis.style.backgroundColor = "white";
  //       // lis.style.color = "#e54847";
  //       lis.style.color = "#777";
  //     };
  //   }
  // }
};
</script>

